<template>
  <div class="home">
    <div>
      <keep-alive><router-view /></keep-alive>
    </div>

    <div>
      <van-tabbar v-model="active" class="nav">
        <van-tabbar-item
          v-for="(item,index) in homeDav"
          :key="index"
          :icon="item.icon"
          @click="changeHome({name:item.type},index)"
        >{{item.title}}</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import { homeDav } from "../data/homeDav.js";

export default {
  name: "home",

  data(){
    return {
      //底部导航栏的数据
      homeDav:[],

      avtive:0

      
    }
  },

  created(){

    this.homeDav = homeDav;

    //获取用户的信息数据
    let defaultActive =localStorage.getItem('defaultActive');

    this.active =parseFloat(defaultActive) 
    // console.log('this.index==>',this.active)

  },

  methods:{
    changeHome(o,index){
      this.$router.push(o)

      // console.log('index==>',index)

      this.defaultActive = index;
      // console.log('this.defaultActive ==>',this.defaultActive)

      // 信息保存到本地
      localStorage.setItem('defaultActive',JSON.stringify(this.defaultActive));

    }
    
  }

};
</script>

<style lang="less" scoped>
.nav {
  background-color: #f5f5f5;
}
</style>
